import React from "react";
// import CompanyName from "./comapny";
import handleClick from "./App";
// Functional Component

// Function Name should start with Capital letter
// Function should return a single element
// Always use className
// Every tag should have closing tag
// Every component have Prop and State

function Card(props) {
  return (
    <div className="card" id={props.product.name}>
      <img src={props.product.image}></img>
      <hr></hr>
      <h4>{props.product.name}</h4>
      <h5>
        Rs.{" "}
        <span className="price">{props.product.price.toLocaleString()}</span>
      </h5>
      <p className="details">{props.product.details}</p>
      <hr></hr>
      <p className="rating">
        {props.product.rating === 5 ? <span>★ ★ ★ ★ ★ </span> : ""}
        {props.product.rating === 4 ? <span>★ ★ ★ ★ ☆ </span> : ""}
        {props.product.rating === 3 ? <span>★ ★ ★ ☆ ☆ </span> : ""}
        {props.product.rating === 2 ? <span>★ ★ ☆ ☆ ☆ </span> : ""}
        {props.product.rating === 1 ? <span>★ ☆ ☆ ☆ ☆ </span> : ""}
      </p>
      <button
        className="btn btn-primary"
        onClick={(e) => {
          props.handleAddCart(e, props.product);
        }}
      >
        Add
      </button>
    </div>
  );
}

export default Card;
