import React, { Component } from "react";
import products from "./items";

function CartItems(props) {
  return (
    <p>
      {props.item}{" "}
      <span>
        <button
          className="btn btn-danger"
          onClick={(e) => {
            props.handleDeleteCart(e, props.item);
          }}
        >
          x
        </button>
      </span>
    </p>
  );
}

export default CartItems;
