import React, { Component } from "react";
let listItems = ["Home", "About", "Services", "Contact"];

function NavBar() {
  return (
    <>
      <div className="col-4">
        <h2>DreamKart</h2>
      </div>
      <div className="col-8 items">
        <ul>
          {listItems.map((item) => {
            return item === "Home" ? (
              <li>
                <a href="#" className="active">
                  {item}
                </a>
              </li>
            ) : (
              <li>
                <a href="#">{item}</a>
              </li>
            );
          })}
        </ul>
      </div>
    </>
  );
}

export default NavBar;
