import logo from "./logo.svg";
import "./App.css";
import Card from "./card";
import NavBar from "./navbar";
import products from "./items";
import { useState } from "react";
import CartItems from "./cartitems";
import Footer from "./footer";

function App() {
  const [cartList, setCartList] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);

  const handleClick = (e, data) => {
    setCartList([...cartList, data.name]);
    setTotalPrice(totalPrice + data.price);
    e.target.setAttribute("disabled", "disabled");
  };

  const handleDelete = (e, data) => {
    console.log(data);
    e.target.parentNode.parentNode.remove();
    let cartItem = document.getElementById(data);
    cartItem.getElementsByTagName("button")[0].removeAttribute("disabled");
    let price = cartItem
      .getElementsByTagName("span")[0]
      .innerText.replace(",", "");
    setTotalPrice(totalPrice - price);
  };
  return (
    <div className="container">
      <div className="row navbar">
        <NavBar></NavBar>
      </div>
      <div className=" row cart">
        <div className="col-3 summary">
          <h3>Your Cart</h3>
          <div className="items">
            {cartList.map((item) => {
              return (
                <CartItems
                  item={item}
                  handleDeleteCart={handleDelete}
                ></CartItems>
              );
            })}

            <p>
              <b>
                Total Price <span>Rs. {totalPrice.toLocaleString()}</span>
              </b>
            </p>
          </div>
          <button className="btn btn-success checkout">
            Proceed to Checkout
          </button>
        </div>
        <div className="col-9 priceCard">
          {products.map((product) => {
            return <Card product={product} handleAddCart={handleClick}></Card>;
          })}
        </div>
      </div>
      <div className="row footer">
        <Footer></Footer>
      </div>
    </div>
  );
}

export default App;
